import { Injectable, Inject } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { DCS_AUTH_CONFIG, AuthConfig } from './auth-lib.module';

const AUTH_TYPE = 'auth-type';

enum AuthType {
  KEYCLOAK = 'keycloak',
  SIGNICAT = 'signicat',
  VEPA = 'vepa',
  CAPTCHA = 'captcha'
}

export interface AccessData {
  accessToken?: string;
  refreshToken?: string;
  redirect?: string;
}

@Injectable({
  providedIn: 'root',
})
export class AuthService {

  constructor(protected httpClient: HttpClient, @Inject(DCS_AUTH_CONFIG) private config: AuthConfig) {
    console.log('[AuthService]', 'constructor', config);
  }

  public oauth(params: any) {
    console.log('[AuthService]', 'oauth');
    if (params.state && params.uri) {
      return this.post(params);
    } else {
      throw new Error('Mandatory parameters state or uri missing');
    }
  }

  public signicat(params: any): Observable<AccessData> {
    console.log('[AuthService]', 'signicat');
    if (params.state && params.uri) {
      this.authType = AuthType.SIGNICAT;
      const body = { code: params.code, uri: params.uri, state: params.state };
      return this.post(body);
    } else {
      throw new Error('Mandatory parameters state or uri missing');
    }
  }

  public vepa(params: any): Observable<AccessData> {
    console.log('[AuthService]', 'vepa');
    if (params.s0 && params.s1 && params.s2 && params.s3 && params.state) {
      this.authType = AuthType.VEPA;
      return this.post(params);
    } else {
      throw new Error('Mandatory parameters s0, s1, s2, s3 or state missing');
    }
  }

  public keycloak(params: any): Observable<AccessData> {
    console.log('[AuthService]', 'keycloak');
    if (params && params.state) {
      params.uri = this.config.redirectUrl;
      this.authType = AuthType.KEYCLOAK;
      return this.post(params);
    } else {
      throw new Error('Mandatory parameters uri or state missing');
    }
  }

  public captcha(params: any): Observable<AccessData> {
    console.log('[AuthService]', 'captcha');
    if (params.token) {
      this.authType = AuthType.CAPTCHA;
      const token = params.token;
      return this.post({ token });
    } else {
      throw new Error('Mandatory parameters token missing');
    }
  }

  public refresh(token: string): Observable<string> {
    console.log('[AuthService]', 'refresh');
    const options = { headers: { Authorization: `Bearer ${token}` } };
    return this.httpClient.post<AccessData>(this.config.refreshUrl, null, options).pipe(
      map((response: AccessData) => {
        return response && response.accessToken;
      })
    );
  }

  private post(body: any): Observable<AccessData> {
    console.log('[AuthService]', 'post', body);
    const options = { headers: { 'X-Auth-Type': this.authType } };
    return this.httpClient.post<AccessData>(this.config.authenticationUrl, body, options).pipe(
      map((response: AccessData) => {
        return response;
      })
    );
  }

  get authType() {
    return sessionStorage.getItem(AUTH_TYPE) as AuthType;
  }

  set authType(type: AuthType) {
    sessionStorage.setItem(AUTH_TYPE, type);
  }
}
